import React from "react";

import CoursList from "../components/dashboard/CoursList";
import Main from "../../components/Main";
const DashboardCours = (props) => {
  if (!props.hide)
    return (
      <Main>
        <CoursList showVideos={props.showVideos} />
      </Main>
    );
  else return <></>;
};

export default DashboardCours;
