import React, {useState,useEffect} from "react";

import "./CoursList.css";
import CourItem from "./CourItem";

  // Redux
import { connect } from "react-redux";


const CoursList = (props) => {

  useEffect(()=>{
    
    


  },[props])

  return props.current_classe.matieres.map((m, key) => {
    return (
      <CourItem
        key={m._id}
        className={`bg-${key % 4}`}
        trimestre={props.user.last_viewed_courses.find(el=>el._id===m._id)?props.user.last_viewed_courses.find(el=>el._id===m._id).temps: "1er cours" }
        // trimestre="1er Trimestre"
        lessonsCount={30}
        matiere={m}
        showVideos={props.showVideos}
      />
    );
  });
};

const mapStateToProps = (state) => {
  return {
    user: state.eleve,
    current_classe: state.eleve.classe
  };
};


export default connect(mapStateToProps, null)(CoursList);