import React from "react";
import { connect } from "react-redux";

const Abonnement = (props) => {
  console.log(props);
  return (
    <div>
      <h1>Abonnement</h1>
      <button
        onClick={() => {
          props.sabonner();
          props.history.push("/cours");
        }}
      >
        S'abonner
      </button>
      <button
        onClick={() => {
          props.disconnect();
        }}
      >
        Deconnection
      </button>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    sabonner: () => dispatch({ type: "ABONNEMENT", token: "azerty" }),
    disconnect: () => dispatch({ type: "DECONNECTION" }),
  };
};

export default connect(null, mapDispatchToProps)(Abonnement);
