import React, { useState } from "react";
import Cards, { Card } from "react-swipe-card";

import CardItem from "./CardItem";
import "./CardsList.css";
import Button from "../../../../shared/components/FormElements/Button";

const CardsList = (props) => {
  const cards = props.cards;
  const [end, setEnd] = useState(false);

  const restartQuiz = () => {
    setEnd(false);
  };

  const endQuiz = () => {
    setEnd(true);
  };
  if (end)
    return (
      <div className="cards-list cards-end">
        <div className="card card-shadow center flex-col">
          <h1>TerminÃ©</h1>
          <h2>Kamal Hekda, rak 9rib...</h2>
          <Button
            onClick={() => {
              props.resetScore();
              restartQuiz();
            }}
          >
            Repasser le Quiz
          </Button>
        </div>
      </div>
    );
  else
    return (
      <Cards
        onEnd={endQuiz}
        className={`cards-list ${end ? "hide" : "no-hide"}`}
      >
        {cards.map((card) => (
          <Card
            key={card}
            onSwipeLeft={props.incrementWrong}
            onSwipeRight={props.incrementCorrect}
          >
            <CardItem card={card} />
          </Card>
        ))}
      </Cards>
    );
};

export default CardsList;
