import React, { useState } from "react";

import "./Quiz.css";
import DashboardQuiz from "./DashboardQuiz";
import CardsQuiz from "./CardsQuiz";
import Main from "../../components/Main";

const Quiz = () => {
  const [isShowQuiz, setIsShowQuiz] = useState(false);
  const [matiereQuiz, setMatiereQuiz] = useState("");

  const showQuiz = (matiere) => {
    setMatiereQuiz(matiere);
    setIsShowQuiz(true);
  };
  const hideQuiz = () => {
    setIsShowQuiz(false);
  };
  return (
    <React.Fragment>
      <Main className="main-quiz">
        <CardsQuiz
          show={isShowQuiz}
          hideQuiz={hideQuiz}
          matiere={matiereQuiz}
        />
        <DashboardQuiz showQuiz={showQuiz} className={isShowQuiz && "hide"} />
      </Main>
    </React.Fragment>
  );
};

export default Quiz;
