import React from "react";

import "./Main.css";
import Profile from "./Profile/Profile";
import Navbar from "./Navigation/Navbar";
const Main = (props) => {
  if (props.profiledisabled)
    return (
      <React.Fragment>
        <main className={`main__noProfile ${props.className}`}>
          {props.children}
        </main>
        <Navbar />
      </React.Fragment>
    );
  else
    return (
      <React.Fragment>
        <Profile />
        <main className={`main ${props.className}`}>{props.children}</main>
        <Navbar />
      </React.Fragment>
    );
};

export default Main;
