import React, { useReducer, useCallback, useState } from "react";
import { Button, Input } from "../../shared/components/FormElements";
import { ReactComponent as EyeVisibility } from "./Eye.svg";

import ArchedHeader from "../../shared/components/UIElements/ArchedHeader";
import AccountService from "../../shared/services/account-service";
import Toasteo from "toasteo";
import "toasteo/dist/css/toasteo.css";
import "./Inscription.css";
import {
  VALIDATOR_REQUIRE,
  VALIDATOR_MINLENGTH,
  VALIDATOR_MAXLENGTH,
} from "../../shared/services/validators";
// Redux
import { connect } from "react-redux";
import { setUserData, CONNECTION, ABONNEMENT } from "../../store/actions";

const formReducer = (state, action) => {
  switch (action.type) {
    case "INPUT_CHANGE":
      let formIsValid = true;
      for (const inputId in state.inputs) {
        if (inputId === action.inputId) {
          formIsValid = formIsValid && action.isValid;
        } else {
          formIsValid = formIsValid && state.inputs[inputId].isValid;
        }
      }
      return {
        ...state,
        inputs: {
          ...state.inputs,
          [action.inputId]: {
            value: action.value,
            isValid: action.isValid,
          },
        },
        formIsValid,
        isSubmitted: false,
      };

    case "FETCH_WILAYAS":
      return {
        ...state,
        wilayas: action.wilayas,
      };

    case "FETCH_COMMUNES":
      return {
        ...state,
        communes: action.communes,
      };

    case "togglePasswordVisibility":
      return {
        ...state,
        inputs: {
          ...state.inputs,
          password: {
            ...state.inputs.password,
            showPassword: !state.inputs.password.showPassword,
          },
        },
      };

    case "SUBMIT":
      return {
        ...state,
        isSubmitted: true,
      };
    case "IS_SUBMITTING":
      return {
        ...state,
        isSubmitting: action.isSubmitting,
      };
    default:
      return state;
  }
};

const Login = (props) => {
  const [formState, dispatch] = useReducer(formReducer, {
    inputs: {
      username: {
        value: "",
        isValid: false,
      },
      password: {
        value: "",
        isValid: false,
      },
    },
    formIsValid: false,
    isSubmitted: false,
    wilayas: [],
    communes: [],
  });

  const changeInputHandler = useCallback(async (id, value, isValid) => {
    dispatch({
      type: "INPUT_CHANGE",
      inputId: id,
      value: value,
      isValid: isValid,
    });
  }, []);

  const onSubmit = (e) => {
    e.preventDefault();
    console.log("Submit");
    console.log(formState);
    if (!formState.formIsValid) {
      dispatch({
        type: "SUBMIT",
      });
      return;
    }

    const data = Object.assign(
      {},
      ...Object.keys(formState.inputs).map((key) => ({
        [key]: formState.inputs[key].value,
      }))
    );
    data.deviceDetect = "Redmi Note 8 Pro, tmtc";

    dispatch({
      type: "IS_SUBMITTING",
      isSubmitting: true,
    });
    window.Toasteo = new Toasteo({});
    // Send to back end
    AccountService.signInClient(data).then(
      (res) => {
        console.log("Sucesss");
        console.log(res);
        dispatch({
          type: "IS_SUBMITTING",
          isSubmitting: false,
        });
        window.Toasteo.success("Authentification RÃ©ussie !");
        // Set Redux state and localstorage entries
        props.confirmLogin({
          client: res.client,
          token: res.token,
        });
        // Redirect user to Dashboard (new abonnement in real)
      },
      (err) => {
        console.error(err);
        window.Toasteo.error(err.message || "echÃ¨c d'authentification");
        dispatch({
          type: "IS_SUBMITTING",
          isSubmitting: false,
        });
      }
    );
  };

  return (
    <div className="inscription login">
      <ArchedHeader />
      {formState.isSubmitting && (
        <div className="loading_overlay">
          <div className="loading_spinner">
            <img src="/assets/imgs/spinner.gif" alt="Loading ..." />
          </div>
        </div>
      )}
      <div className="inscription__content">
        <p className="inscription__title">
          Se connecter
          {formState.isSubmitted && !formState.formIsValid && (
            <span
              style={{
                fontSize: "80%",
                fontWeight: "500",
                color: "red",
                display: "block",
              }}
            >
              Veuillez remplir les champs avec des donnÃ©es valides!
            </span>
          )}
        </p>

        <form className="inscription__form" onSubmit={onSubmit}>
          <Input
            element="input"
            id="username"
            type="text"
            validators={[
              VALIDATOR_REQUIRE(),
              VALIDATOR_MINLENGTH(3),
              VALIDATOR_MAXLENGTH(24),
            ]}
            placeholder="Votre nom de compte (requis)"
            errorText="Veuilez entrer un nom de compte valide de 3 Ã  24 charactÃ¨res!"
            onInput={changeInputHandler}
          />

          <div className="inscription__password">
            <Input
              element="input"
              id="password"
              type={
                formState.inputs.password.showPassword ? "text" : "password"
              }
              icon={EyeVisibility}
              onIconClick={() => dispatch({ type: "togglePasswordVisibility" })}
              iconClassName={
                formState.inputs.password.showPassword ? "active" : ""
              }
              validators={[
                VALIDATOR_REQUIRE(),
                VALIDATOR_MINLENGTH(6),
                VALIDATOR_MAXLENGTH(24),
              ]}
              errorText="Veuillez entrer un mot de passe de 6 Ã  24 charactÃ¨res."
              placeholder="Votre mot de passe (requis)"
              onInput={changeInputHandler}
            />
          </div>
          <Button type="submit" className="inscription__form-sinscrire">
            Se connecter
          </Button>
        </form>

        <footer>
          <div className="inscription__footer-content">
            <p>Vous Ãªtes nouveau ?</p>
            <div className="inscription__footer-actions">
              <hr />
              <Button
                transparent
                type="link"
                to="/inscription"
                className="inscription__login"
              >
                S'inscrire
              </Button>
              <hr />
            </div>
          </div>
        </footer>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    confirmLogin: (params) => {
      dispatch(setUserData(params));
      dispatch({
        type: CONNECTION,
        token: params.token,
      });
      if (params.client.is_abonne)
        dispatch({
          type: ABONNEMENT,
          token: params.token,
        });
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);
