import React, { useState } from "react";

import "./Profile.css";
import DrawerProfile from "./DrawerProfile";
import { ReactComponent as IconSubActifCheck } from "./img/icon_subActif_check.svg";
  // Redux
import { connect } from "react-redux";

const Profile = (props) => {
  const [isDrawer, setIsDrawer] = useState(false);

  const openDrawer = () => {
    setIsDrawer(true);
  };

  const closeDrawer = () => {
    setIsDrawer(false);
  };
  return (
    <React.Fragment>
      <DrawerProfile cancel={closeDrawer} show={isDrawer} />
      <div className="profile">
        {isDrawer && (
          <button onClick={closeDrawer} className="profile__cancel">
            <span></span>
            <span></span>
          </button>
        )}
        <div className="profile__content">
          <button className="profile__menu" onClick={openDrawer}>
            <span></span>
            <span></span>
            <span></span>
          </button>
          <div className="profile__info">
            <h2>{props.user && props.user.nom}</h2>
            <h3>{props.user && props.user.classe.codeCl}</h3>
          </div>
          <div className="profile__sub">
            {props.account && props.account.is_abonne ? (
              <span className="sub-actif">
                <p>Abonnement Actif</p>
                <IconSubActifCheck className="i-subActif-check" />
              </span>
            ) : (
              <span className="sub-inactif">Non AbonnÃ©e </span>
            )}
            <div className=""></div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.eleve,
    account: state.user
  };
};


export default connect(mapStateToProps, null)(Profile);