const USER_API=SERVEUR_URL+'/api/user';

/**
 * Fetch all courses names and videos of a specifi matiere for a specific classe.
 * @param {*} data 
 */
const getMatiereVideos = (data) => {
    if (checkRoleAutorizationFail(data,"eleve"))
        return Promise.reject({
        message:
            "Non autorisÃ©! Uniquement l'Ã©lÃ¨ve concernÃ© qui peut effectuer cette tÃ¢che",
        });
    const params = [
        "userType",
        "matiereId",
        "classeId"
        ];
    const keys=Object.keys(data);
    if (!params.every((key) => keys.includes(key)))
    return Promise.reject({
        message: "Les paramÃ¨tres de formulaire sont invalides",
    });
  const requestOptions = {
    method: "GET",
    headers: {...authHeader("eleve"),userType: data.userType},
  };

  return fetch(`${USER_API}/cours/classe/matiere/index/${data.classeId}/${data.matiereId}`, requestOptions).then(
    handleResponse
  );
};

