export const CONNECTION = "CONNECTION";
export const ABONNEMENT = "ABONNEMENT";
export const DESABONNEMENT = "DESABONNEMENT";
export const DECONNECTION = "DECONNECTION";
export const CURRENTLINK = "CURRENTLINK";
export const CURRENTMATIERE = "CURRENTMATIERE";
export const USER = "USER";
export const ELEVE = "ELEVE";


export const setUserData=(payload)=>{
    localStorage.setItem('client',JSON.stringify(payload.client));
    localStorage.setItem('token',payload.token);
    return {
        type:USER,
        user: payload.client
    };
}

export const setEleveData=(payload)=>{
    localStorage.setItem('eleve',JSON.stringify(payload.eleve));
    localStorage.setItem('token',payload.token);
    return {
        type:ELEVE,
        user: payload.eleve
    };
}
