import React from "react";

import "./ListSommaire.css";
import ItemSommaire from "./ItemSommaire";
const ListSommaire = (props) => {
  return (
    <ul className="list-sommaire">
      {props.videos.map((video) => (
        <ItemSommaire
          key={video.id}
          id={video.id}
          className={video.id === props.videoPlay.id ? "active" : ""}
          url={video.url}
          openVideo={props.openVideo}
        />
      ))}
    </ul>
  );
};

export default ListSommaire;
