import React from "react";
import "./App.css";
import { Route, Switch, Redirect } from "react-router-dom";
import { connect } from "react-redux";

import Preview from "./preview/pages/Preview";
import Cours from "./dashboard/cours/pages/Cours";
import Videos from "./dashboard/cours/pages/Videos";
import Bibliotheque from "./dashboard/bibliotheque/pages/Bibliotheque";
import SommaireBibliotheque from "./dashboard/bibliotheque/pages/Sommaire";
import Quiz from "./dashboard/quiz/pages/Quiz";
import Abonnement from "./abonnement/pages/Abonnement";
import { Login, Inscription,LoginEleve } from "./auth";
import { deviceDetect } from "react-device-detect";

function App(props) {
  let routes;
  if (props.is_connecte) {
    if (props.is_abonne)
      if(props.is_eleve_connecte)
        routes = (
          <React.Fragment>
            <Route path="/cours" exact component={Cours} />
            <Route path="/cours/videos" exact component={Videos} />
            <Route path="/bibliotheque" exact component={!props.current_matiere ||props.current_matiere===null?Bibliotheque:SommaireBibliotheque} />
            <Route path="/quiz" exact component={Quiz} />
            <Redirect to="/cours" />
          </React.Fragment>
        );
      else
        routes = (
          <React.Fragment>
            <Route path="/completelogin" exact component={LoginEleve} />
            
            <Redirect to="/completelogin" />
          </React.Fragment>
        );

    else
      routes = (
        <React.Fragment>
          <Route path="/abonnement/new" component={Abonnement} />

          <Redirect to="/abonnement/new" />
        </React.Fragment>
      );
  } else
    routes = (
      <React.Fragment>
        <Route path="/login" component={Login} />
        <Route path="/inscription" component={Inscription} />
        <Route path="/" exact component={Preview} />
        <Redirect to="/" />
      </React.Fragment>
    );
  return (
    <div className="app-user">
      <Switch>{routes}</Switch>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    is_connecte: state.is_connecte,
    is_eleve_connecte: state.is_eleve_connecte,
    is_abonne: state.is_abonne,
    current_matiere: state.current_matiere,
  };
};

export default connect(mapStateToProps, null)(App);
