import React from "react";

import VideoItem from "./VideoItem";
import "./VideosList.css";
const VideosList = (props) => {
  return (
    <ul className={`videos-list ${props.className}`}>
      {props.videos.map((video) => (
        <VideoItem
          key={video.id}
          openVideo={props.openVideo}
          url={video.url}
          id={video.id}
          className={`bg-thumbnail-${Number(video.id) % 3}`}
          active={video.id === props.videoPlay.id && "active"}
        />
      ))}
    </ul>
  );
};

export default VideosList;
