import React , {useState, useEffect} from "react";

import "./Sommaire.css";
import Main from "../../components/Main";
import ContentService from "../../../shared/services/content-service"
  // Redux
import { connect } from "react-redux";
const SommaireBibliotheque = (props) => {

  const [listePdfs,setListPdfs]=useState([]);

  useEffect(()=>{
    const data={
      userType: "eleve",
      matiereId: props.current_matiere._id,
      classeId: props.current_classe._id,
    }
    ContentService.getMatierePDFs(data).then(
      res=>{
        setListPdfs(res.courses)

      },
      err=>{
        console.error(err);
        
      }
    )


  },[props])

  return (
    <Main>
      <h1 className="page-head-title">Bibliotheque</h1>
      <div className="container">
        <div className="grid-row">
          {listePdfs.map((book,index)=>(
            <div className="grid-item" key={index}>
              <div className="grid-item-container">
                <div className="book-icon-div">
                  <img src="/assets/img/pdf.svg" alt ="" />

                </div>
                <div className="book-infos-div">
                  <div className="book-infos-cours-title">
                    {book.titre}
                    
                  </div>
                  <div className="book-infos-pdf-title">
                    {book.titrePdf}

                  </div>
                  <div className="book-infos-timestamp">
                    {book.date_ajout}

                  </div>

                </div>

              </div>

            </div>
          ))}
          

        </div>
        
      </div>

    </Main>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.eleve,
    current_classe: state.eleve.classe,
    current_matiere: state.current_matiere
  };
};


export default connect(mapStateToProps, null)(SommaireBibliotheque);