import React, { useReducer, useEffect } from "react";
import "./Input.css";
import { validate } from "../../services/validators";

const inputReducer = (state, action) => {
  switch (action.type) {
    case "CHANGE":
      return {
        ...state,
        value: action.val,
        isValid: validate(action.val, action.validators),
      };
    case "TOUCH":
      return {
        ...state,
        isTouched: true,
      };
    default:
      return state;
  }
};

const Input = (props) => {
  const [inputState, dispatch] = useReducer(inputReducer, {
    value: "",
    isValid: props.required === false ? true : false,
    isTouched: false,
  });

  const { id, onInput } = props;
  const { value, isValid } = inputState;
  useEffect(() => {
    onInput(id, value, isValid);
  }, [inputState]);

  const changeHandler = (e) => {
    dispatch({
      type: "CHANGE",
      val: e.target.value,
      validators: props.validators,
    });
  };

  const touchHandler = (e) => {
    dispatch({
      type: "TOUCH",
    });
  };
  let element;
  switch (props.element) {
    case "input":
      if (props.icon)
        element = (
          <div className="input-icon">
            <input
              id={props.id}
              type={props.type}
              placeholder={props.placeholder}
              onChange={changeHandler}
              onBlur={touchHandler}
              value={inputState.value}
            />
            <div
              className={` icon ${props.iconClassName && props.iconClassName}`}
              onClick={props.onIconClick && props.onIconClick}
            >
              <props.icon />
            </div>
          </div>
        );
      else
        element = (
          <input
            id={props.id}
            type={props.type}
            placeholder={props.placeholder}
            onChange={changeHandler}
            onBlur={touchHandler}
            value={inputState.value}
          />
        );
      break;
    case "textarea":
      element = (
        <textarea
          id={props.id}
          onBlur={touchHandler}
          rows={props.row || "3"}
          onChange={changeHandler}
          value={inputState.value}
        />
      );
      break;
    case "select":
      element = (
        <select
          id={props.id}
          onBlur={touchHandler}
          className={`${
            (inputState.value === "" || !inputState.value) && "placeholder"
          }`}
          onChange={changeHandler}
          defaultValue={props.placeholder}
        >
          <option
            style={{ color: "rgba(0,0,0,.29)" }}
            value={props.placeholder}
            disabled
          >
            {props.placeholder}
          </option>
          {props.options &&
            props.options.map &&
            props.options.map((opt, i) => (
              <option
                style={{ color: "black" }}
                key={i}
                value={opt.code ? `${opt.code}-${opt.nom}` : `${opt.nom}`}
              >
                {opt.code ? `${opt.code}-` : ``}
                {opt.nom}
              </option>
            ))}
        </select>
      );
  }
  return (
    <div
      className={`form-control ${props.form_className && props.form_className} 
      ${inputState.isTouched && !inputState.isValid && "form-control-invalid"}`}
    >
      {props.label && <label htmlFor={props.id}>{props.label}</label>}
      {element}
      {inputState.isTouched && !inputState.isValid && <p>{props.errorText}</p>}
    </div>
  );
};

export default Input;
