import React, { useState } from "react";

import DashboardCours from "./DashboardCours";
import Videos from "./Videos";

const Cours = () => {
  const [isShowVideos, setIsShowVideos] = useState(false);
  const [matiereVideos, setMatiereVideos] = useState("");

  const showVideos = (matiere) => {
    setMatiereVideos(matiere);
    setIsShowVideos(true);
  };
  const hideVideos = () => {
    setIsShowVideos(false);
  };
  return (
    <React.Fragment>
      <Videos
        show={isShowVideos}
        hideVideos={hideVideos}
        matiere={matiereVideos}
      />
      {!isShowVideos && <DashboardCours showVideos={showVideos} />}
    </React.Fragment>
  );
};

export default Cours;
