import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

import "./LinksDrawer.css";
const LinksDrawer = (props) => {
  return (
    <ul className={`links-drawer ${props.className}`}>
      {props.links.map((link) => (
        <li key={link} className={link===props.current_link?"active":""}>
          {link === "deconnexion" ? (
            <Link onClick={props.deconnection} to="/">
              {link.charAt(0).toUpperCase() + link.slice(1)}
            </Link>
          ) : (
            <Link to={`/${link}`} onClick={(e)=>props.setCurrentLink(link)}>
              {link.charAt(0).toUpperCase() + link.slice(1)}
            </Link>
          )}
        </li>
      ))}
    </ul>
  );
};

const mapStateToProps = (state) => {
  return {
    current_link: state.current_link
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    deconnection: () => dispatch({ type: "DECONNECTION" }),
    setCurrentLink: (link) => dispatch({ type: "CURRENTLINK", value: link }),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LinksDrawer);
