import * as actionTypes from "./actions";

const initState = () => {
  const cachedUser =
    localStorage.getItem("client") != null
      ? JSON.parse(localStorage.getItem("client"))
      : null;
  const cachedEleve =
    localStorage.getItem("eleve") != null
      ? JSON.parse(localStorage.getItem("eleve"))
      : null;
  const cachedMatiere =
    localStorage.getItem("current_matiere") !== null
      ? JSON.parse(localStorage.getItem("current_matiere"))
      : cachedEleve !== null
      ? cachedEleve.classe.matieres[1]
      : null;
  const initialState = {
    current_link: null,
    is_connecte: cachedUser !== null || false,
    is_eleve_connecte: cachedEleve !== null || false,
    is_abonne: (cachedUser !== null && cachedUser.is_abonne) || false,
    token: localStorage.getItem("token"),
    user: cachedUser,
    eleve: cachedEleve,
    current_matiere: cachedMatiere,
  };

  return initialState;
};

const reducer = (state = initState(), action) => {
  switch (action.type) {
    case actionTypes.CURRENTLINK:
      // Set the current page active in drawer sidebar
      return {
        ...state,
        current_link: action.value,
      };
    case actionTypes.CURRENTMATIERE:
      // Set the current page active in drawer sidebar
      return {
        ...state,
        current_matiere: action.value,
      };
    case actionTypes.CONNECTION:
      // Save local storage!
      return {
        ...state,
        is_connecte: true,
        token: action.token,
      };

    case actionTypes.DECONNECTION:
      localStorage.removeItem("client");
      localStorage.removeItem("token");
      return {
        ...state,
        is_connecte: false,
        is_abonne: false,
      };

    case actionTypes.ABONNEMENT:
      return {
        ...state,
        is_abonne: true,
        is_connecte: true,
        token: action.token,
      };

    case actionTypes.DESABONNEMENT:
      // Remove and Save new local storage
      return {
        ...state,
        is_abonne: false,
      };
    case actionTypes.USER:
      // Set the authenticated user
      return {
        ...state,
        user: action.user,
      };
    case actionTypes.ELEVE:
      // Set the authenticated eleve
      return {
        ...state,
        eleve: action.eleve,
        is_eleve_connecte: true,
      };
    default:
      return state;
  }
};

export default reducer;
