import React from "react";

import "./QuizItem.css";
const QuizItem = (props) => {
  return (
    <li className="quiz-item">
      <button
        onClick={() => {
          props.showQuiz(props.matiere);
        }}
        className={` quiz-item__thumbnail ${props.className}`}
      >
        <div className="quiz-item__thumbnail-matiere">
          <p>{props.matiere}</p>
        </div>
        <div className="quiz-item__thumbnail-title">
          <p>Quiz</p>
          <p>{props.matiere}</p>
        </div>
        <div className="quiz-item__thumbnail-questions">
          <p>{props.questionsCount} Questions</p>
        </div>
      </button>
    </li>
  );
};

export default QuizItem;
