import React , {useState, useEffect} from "react";

import "./Bibliotheque.css";
import Main from "../../components/Main";
import FolderItem from "../components/Item";
  // Redux
import { connect } from "react-redux";
const Bibliotheque = (props) => {


  const handleFolderClick=(matiere)=> props.setCurrentMatiere(matiere)
      
  return (
    <Main>
      <h1 className="page-head-title">Bibliotheque</h1>
      <div className="matieres-container">

          <ul className="quiz-list">
          {props.matieres.map((m, key) => {
            return (
              
                <FolderItem
                key={key}
                className={`bg-${key % 2}`}
                matiere={m}
                clickHandler={(e)=>handleFolderClick(m)}
              />
              
              
            );
          })}
        </ul>
      </div>

    </Main>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.eleve,
    matieres: state.eleve.classe.matieres,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setCurrentMatiere: (matiere)=>{
      dispatch({type: "CURRENTMATIERE", value: matiere});
      localStorage.setItem('current_matiere',JSON.stringify(matiere));   
    }
   
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Bibliotheque);