import React from "react";
import { Link } from "react-router-dom";

import "./Button.css";
const Button = (props) => {
  if (props.to) {
    return (
      <Link
        to={props.to}
        className={`button ${props.gradient && "button--gradient"} ${
          props.transparent && "button--transparent"
        } ${props.className}`}
        style={props.style}
      >
        {props.children}
      </Link>
    );
  }
  return (
    <button
      onClick={props.onClick}
      className={`button ${props.gradient && "button--gradient"} ${
        props.transparent && "button--transparent"
      } ${props.className}`}
      type={props.type}
      href={props.href}
      style={props.style}
    >
      {props.children}
    </button>
  );
};

export default Button;
