import React, { useRef, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";

import SwipeCore, { Pagination } from "swiper";
import "swiper/swiper-bundle.css";
import "./Preview.css";
import PreviewTab from "../components/PreviewTab";
import Button from "../../shared/components/FormElements/Button";

SwipeCore.use([Pagination]);
const Preview = (props) => {
  const previewPages = [];
  for (let i = 0; i < 3; i++) {
    previewPages.push(
      <SwiperSlide key={`slide-${i}`}>
        <PreviewTab id={Number(i + 1)} />
      </SwiperSlide>
    );
  }

  const previewActionsRef = useRef();

  // style the pagination bullet on top of the buttons
  useEffect(() => {
    if (previewActionsRef.current) {
      const swiperPagination = document.querySelector(".swiper-pagination");
      const rectPreviewActions = previewActionsRef.current.getBoundingClientRect();

      swiperPagination.style.top = rectPreviewActions.y - 10 + "px";
    }
  }, [previewActionsRef]);

  return (
    <div className="preview">
      <div className="preview__swipe">
        <Swiper pagination loop="infinity" className="preview__swipe" id="main">
          {previewPages}
        </Swiper>
        <div ref={previewActionsRef} className="preview__actions">
          <Button
            onClick={() => {
              props.history.push("/login");
            }}
          >
            Se connecter
          </Button>
          <Button
            transparent
            style={{ padding: 0, margin: 0 }}
            onClick={() => {
              props.history.push("/inscription");
            }}
          >
            S'inscrire
          </Button>
        </div>
      </div>
    </div>
  );
};
export default Preview;
