/**
 * Check if the requester is the right user.
 * @param {*} data 
 */  
const checkRoleAutorizationFail = (data,role="client") => {
    return !(data.userType && data.userType === role);
  };
  

/**
 * Add token Authorization entry to header request.
 */

function authHeader (type='client') {
    let token =localStorage.getItem('token')
  
    if (token) {
      return { 'Authorization': 'Bearer ' + token }
    } else {
      return {'Authorization':''}
    }
  }


/**
 * handle server response, and parse it.
 * @param {*} response 
 */
function handleResponse (response) {
    return response.text().then(text => {
      const data = text && JSON.parse(text)
      if (!response.ok) {
        const error = (data && data.message) || response.statusText
        return Promise.reject(error)
      }
  
      return Promise.resolve(data)
    })
  }

export {handleResponse,authHeader,checkRoleAutorizationFail}  
