import React, {useState } from "react";
import ArchedHeader from "../../shared/components/UIElements/ArchedHeader";
import AccountService from "../../shared/services/account-service";
import Toasteo from "toasteo";
import "toasteo/dist/css/toasteo.css";
import "./Inscription.css";
import "./LoginEleve.css";

  // Redux
import { connect } from "react-redux";
import {setEleveData} from "../../store/actions";


const LoginEleve = (props) => {
  
  const [isSubmitting,setTisSubmitting]=useState(false);

  


  const connectEleve = (eleveId) => {
    
    const data={
      userType:"client",
      eleveId:eleveId
    }
    setTisSubmitting(true);
    window.Toasteo = new Toasteo({});
    // Send to back end
    AccountService.signInEleve(data).then(
      res=>{
        console.log('Sucesss');
        console.log(res);
        setTisSubmitting(false);
        window.Toasteo.success('Authentification RÃ©ussie !');

        props.setCurrentMatiere(res.eleve.classe.matieres[0]);
          // Set Redux state and localstorage entries
        props.confirmLogin({
          eleve: res.eleve,
          token: res.token
        });
          // Redirect user to Dashboard (new abonnement in real) 
          

      },
      err=>{
        console.error(err);
        
        window.Toasteo.error(err.message || "echÃ¨c d'authentification");
        setTisSubmitting(false);


      }
    )
  };

  return (
    <div className="inscription">
      <ArchedHeader />
      {isSubmitting && <div className="loading_overlay">
        <div className="loading_spinner"><img src="/assets/imgs/spinner.gif" alt="Loading ..."/>
        </div>
        </div>
      }
      <div id="select_eleve_modal" className="modal">

        <div className="modal-content">
          <div className="modal-container">
            {/* <span class="close">&times;</span> */}
            
            {props.eleves.map((eleve,index)=>(
              <div className={"eleve-card "+(eleve.sexe==='M'?"male-bg":"female-bg")} key={index}> 
                <div className="eleve-card-header">
                  <div className="eleve-card-avatar" onClick={(e)=>connectEleve(eleve._id)}>
                    <img src="/assets/img/footer_bg.jpg" alt ="" />
                    <h5 className="eleve-card-avatar-name"> {eleve.nom}</h5>

                  </div>

                </div>
              </div>
            ))}

          </div>
        </div>

      </div>
    

      
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.user,
    eleves: state.user.eleves
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    confirmLogin: (params)=>{
      dispatch(setEleveData(params));
      
      
    },
   
    setCurrentMatiere: (matiere)=>{
      dispatch({type: "CURRENTMATIERE", value: matiere});
      localStorage.setItem('current_matiere',JSON.stringify(matiere));   
    }
   
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginEleve);

